<template>
  <Tooltip :content="tips">
    <button :class="btnClass" v-on:click="onClick">
      <svgicon :name="name" width="16" height="16" :color="svgcolor"></svgicon>
    </button>
  </Tooltip>
</template>
<script>
import "../icons";
const svgcolor_on = "#42b983 #35495e";
const svgcolor_off = "#35495e #35495e";
export default {
  name: "SvgButton",
  computed: {
    btnClass() {
      let ret = "icon-button";
      if (this.onOff == undefined) {
        return ret;
      }
      ret = ret + (this.on ? " enalbe_ul_btn" : " disable_ul_btn");
      return ret;
    },
    svgcolor() {
      if (this.onOff) {
        return this.on ? svgcolor_on : svgcolor_off;
      }
      return svgcolor_on;
    },
  },
  data() {
    return {};
  },

  props: {
    onClick: {
      type: Function,
      default: undefined,
    },
    name: { type: String, default: "" },
    tips: { type: String, default: "" },
    onOff: { type: Boolean, default: undefined },
    on: { type: Boolean, default: undefined },
  },
  methods: {},
};
</script>
<style scoped>
.icon-button {
  height: 22px;
  padding: 1px;
  width: 22px;
  margin: 4px;
  border: 1px;
  border-radius: 3px;
}
button span {
  width: fit-content;
  position: absolute;
  margin: 4px;
  padding: 5px;
  /* left: 10px; */
  border-radius: 3px;
  top: 28px;
  border: 1px solid #42b983;
  background-color: white;
  font-size: 12px;
  line-height: 20px;
  color: #42b983;
}
.disable_ul_btn {
  border: 1px solid gray;
}
.enalbe_ul_btn {
  border: 1px solid red;
}
</style>